.body {
      height: 100vh;
      background-color: #1D1D1D;
      position: relative;
}

.wrapper {
      width: 950px;
      height: 600px;
      position: absolute;
      left:0;
      right:0;
      top:0;
      bottom:0;
      margin: auto;
}

.content {
      position: relative;
      width: 100%;
      height: 100%;
}

.content1 {
      position: absolute;
      right: 0;
      top: 0;
      width: 700px;
      height: 500px;
      background-color: #CFCBC8;
      border-radius: 3px;
}

.content2 {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 400px;
      height: 500px;
      background-color: #A82400;
      border-radius: 3px;
}


