.body {
      background-color: #3FFFDC;
      height: 85vh;
      position: relative;
}

.wrapper {
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      height: 80%;
      width: 50%;
      margin: auto;
      position: absolute;
}

.maintitle {
      width: 100%;
      height: 70%;
      position: relative;
      text-align: center;
}

.maintitlecenter {
      font-size: 20rem;
      font-weight: bold;
      height: 100%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.bold {
      font-weight: 600;
      font-size: 1.3rem;
}

.topleft {
      position: absolute;
      left: 0;
      top: 0;
}

.topright {
      position: absolute;
      right: 0;
      top: 0;
}

.bottomleft {
      position: absolute;
      left: 0;
      bottom: 0;
}

.bottomright {
      position: absolute;
      right: 0;
      bottom: 0;
}

.footer {
      height: 22%;
      margin-top: 8%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-line-pack: center;
          align-content: center;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-direction: column;
          flex-direction: column;
}

.footertext {
      font-size: 1.5rem;
      font-weight: bold;
      line-height: 1rem;
      padding: 2rem 0;
      border-top: 2px solid #0E3530;
      border-bottom: 2px solid #0E3530;
      text-align: center;
}


