.body {
      background-color: #1D1D1D;
      height: 50vh;
      position: relative;
}

.wrapper {
      width: 60%;
      height: 70%;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      color: white;
      font-size: 2rem;
      font-weight: 100;
      line-height: 4rem;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-line-pack: center;
          align-content: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.strong {
      font-size: 2.5rem;
      font-weight: 700;
}


